.qubo-logo {
  --size: 30rem;
  --gap: 1rem;
  --top-scale: .57735;
  --gap-scale: .86603;
  --side-scale: 1.1547;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.qubo-logo .top {
  width: var(--size);
  height: var(--size);
  background-position: 25%;
  background-repeat: no-repeat;
  background-size: cover;
  margin-block-start: calc((var(--top-scale)  - 1) * var(--size));
  margin-block-end: calc(var(--gap-scale) * var(--gap));
}

.qubo-logo .top.flag-pride {
  background-image: url("flag-pride.3e530828.svg");
}

.qubo-logo .top.flag-progress-1 {
  background-image: url("flag-progress-1.6ec431c7.svg");
}

.qubo-logo .top.flag-progress-2 {
  background-image: url("flag-progress-2.268dacbb.svg");
}

.qubo-logo .top.angle-horizontal {
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  transform: scaleY(var(--top-scale));
  transform-origin: bottom;
}

.qubo-logo .top.angle-diagonal {
  transform-origin: 0 100%;
  margin-inline-start: var(--size);
  transform: scale(.707107, .408248)rotate(-45deg);
}

.qubo-logo .sides {
  font-size: calc(.3 * var(--size));
  justify-content: center;
  gap: var(--gap);
  font-weight: bold;
  display: flex;
}

.qubo-logo .sides > * {
  width: calc(var(--size) / 2);
  height: calc(var(--side-scale) * var(--size) / 2);
  align-items: center;
  display: flex;
}

.qubo-logo .sides .left {
  transform-origin: 100% 100%;
  transform: skewY(30deg);
}

.qubo-logo .sides .right {
  transform-origin: 0 100%;
  transform: skewY(-30deg);
}

.qubo-logo .sides .text {
  text-align: center;
  width: 100%;
}

.qubo-logo .sides.stripes {
  color: var(--azul-caixa);
}

.qubo-logo .sides.stripes > * {
  border-block: calc(.1 * var(--size)) solid var(--azul-caixa);
  background: #fff;
}

.qubo-logo .sides.stripes .left {
  border-block-end-color: var(--laranja-caixa);
}

.qubo-logo .sides.stripes .right {
  border-block-start-color: var(--laranja-caixa);
}

.qubo-logo .sides.solid-bg, .qubo-logo .sides.no-text {
  color: #fff;
}

.qubo-logo .sides.solid-bg .left, .qubo-logo .sides.no-text .left {
  background: var(--azul-caixa);
}

.qubo-logo .sides.solid-bg .right, .qubo-logo .sides.no-text .right {
  background-color: var(--laranja-caixa);
}

.qubo-logo .sides.no-text .text {
  display: none;
}

.qubo-logo.bg-black {
  background: #000;
}

.qubo-logo.bg-blue {
  background: var(--azul-caixa);
}

.qubo-logo.bg-white {
  background: #fff;
}

@media (width >= 520px) and (height >= 600px) {
  .qubo-logo {
    --size: 50rem;
  }
}

.options {
  max-width: 50ch;
  margin-block: 2rem;
  margin-inline: auto;
  padding-inline: 1rem;
}

.options .field {
  align-items: baseline;
  gap: 1rem;
  width: 100%;
  margin-block: 1rem;
  font-size: 1.8rem;
  display: flex;
}

.options .field label {
  text-align: right;
  flex: 1;
  min-width: 12rem;
}

.options .field select {
  font-family: inherit;
  font-size: inherit;
  background: #fff;
  border: .1rem solid gray;
  border-radius: 1rem;
  flex: 5;
  padding: .5rem 1rem;
}
